.mainList
{
  position:absolute;
  top: 856px;
  display:flex;
  width: 100%;
  margin-bottom: 100px;
  margin-top: 100px;
  
   
    
}
.topicList
{
   
    font-family: 'Roboto', sans-serif;
    word-spacing: 0px;
    text-align:center;
    width: 100%;
  
    
}
.imgTopic
{
    width: 200px;
    height: 200px;
    border-radius: 50%;
    margin-bottom: 5px;
    
    
}
.h1Topic
{
    width: 100%;
    text-align:center;
    color: #00FF96;
    text-shadow: -1px 0 #125e54, 0 1px #125e54, 1px 0 #125e54, 0 -1px #125e54;
}
.pTopic
{
    width: 100%;
    text-align:center;
    margin-bottom: 30px;
}
.buttonTopic
{
    text-decoration: none;
    color: #00FF96;
    background-color: #125e54;
    border: solid 2px #00FF96;
    padding: 10px;
    border-radius: 20%;
    
    
    
}
@media (max-width: 768px)
{
    .mainList
    {
        flex-direction: column;
        top: 356px;
        margin: 0;
    }
    .imgTopic
    {
        margin-top: 100px;
    }
}